html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-monster);
  background-color: var(--bg-cream);
  color: var(--grayishBlue);
  min-height: 100vh;
  font-size: 14px;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --darkCyan: hsl(158, 36%, 37%);
  --bg-cream: hsl(30, 38%, 92%);
  --darkBlue: hsl(212, 21%, 14%) ;
  --grayishBlue: hsl(228, 12%, 48%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-france: "Fraunces", serif;
  --font-monster: "Montserrat", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0.625rem;
  font-family: var(--font-france);
  font-size: 2.3125rem;
  line-height: 1;
  color: var(--darkBlue);
}
@media (max-width: 71.8125em) {
  h1,
  h2,
  h3 {
    font-size: 1.875rem;
    margin-top: 0;
    margin-bottom: 0;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.working_area {
  height: 98vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  height: 30rem;
  width: 37.5rem;
  background-color: var(--white);
  border-radius: 0.625rem;
  display: flex;
}
@media (max-width: 71.8125em) {
  .card {
    height: 43.75rem;
    width: 88%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.pdt_img {
  border-top-left-radius: 0.625rem;
  border-bottom-left-radius: 0.625rem;
  overflow: hidden;
  width: 21.875rem;
  height: 100%;
}
@media (max-width: 71.8125em) {
  .pdt_img {
    height: 18.75rem;
    width: 100%;
    border-bottom-left-radius: 0;
    border-top-right-radius: 0.625rem;
  }
}
.pdt_img img {
  width: 100%;
  height: 100%;
}
@media (max-width: 71.8125em) {
  .pdt_img img {
    content: url("/images/image-product-mobile.jpg");
    width: 100%;
    height: 100%;
  }
}

.pdt_desc {
  width: 50%;
  height: 25rem;
  margin-left: 1.875rem;
  margin-right: 1.5625rem;
  margin-top: 0.9375rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: baseline;
}
@media (max-width: 71.8125em) {
  .pdt_desc {
    height: 50%;
    width: 80%;
    font-size: 0.75rem;
  }
}

.pdt {
  letter-spacing: 0.3125rem;
  margin-top: 3.125rem;
  margin-bottom: 0.625rem;
}
@media (max-width: 71.8125em) {
  .pdt {
    margin-top: 0;
  }
}

.desc {
  margin-top: 0;
  word-spacing: 0.1875rem;
  font-size: 0.875rem;
  line-height: 1.45;
}
@media (max-width: 71.8125em) {
  .desc {
    font-size: 0.75rem;
    word-spacing: 0.15625rem;
    line-height: 1.5;
    margin-bottom: 0;
  }
}

.price {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
  margin-top: 0.625rem;
  margin-bottom: 1.875rem;
}
@media (max-width: 71.8125em) {
  .price {
    margin-bottom: 0;
    margin-top: 0;
  }
}
.price h1 {
  margin: 0;
  color: var(--darkCyan);
}
.price p {
  text-decoration: line-through;
}

button {
  width: 100%;
  font-weight: 700;
  line-height: 3.125rem;
  color: var(--white);
  font-family: var(--font-monster);
  font-size: 0.875rem;
  border-radius: 0.625rem;
  border: none;
  background-color: var(--darkCyan);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;
}

button:hover {
  background-color: #0c4530;
  color: var(--white);
  cursor: pointer;
}